/* 
    Fonts mobile (max md)
    exemple d'utilisation :
        .h1 {
            font: var(--h1);
        }
*/
@media screen and (max-width: 768px) {
    html {
        /* titres */
        --h1: 700 1.875rem  "Roboto Slab", serif;
        --h2: 500 1.563rem  "Roboto", sans-serif;
        --h3: 500 1.25rem  "Roboto", sans-serif;
        --h4: 500 1.25rem  "Roboto", sans-serif;
        --h5: 500 1.25rem  "Roboto", sans-serif;
        
        /* contenu */
        --p: 400 1.25rem  "Roboto", sans-serif;
        --label: 300 1.25rem  "Roboto", sans-serif;
    }
}

/* 
    Fonts pc (min md)
    exemple d'utilisation :
        .h1 {
            font: var(--h1);
        }
*/
@media screen and (min-width: 768px) {
    html {
        /* titres */
        --h1: 700 4.375rem  "Roboto Slab", serif;
        --h2: 700 3.75rem  "Roboto Slab", serif;
        --h3: 500 3.75rem  "Roboto", sans-serif;
        --h4: 500 1.563rem  "Roboto", sans-serif;
        --h5: 500 1.25rem  "Roboto", sans-serif;
        
        /* contenu */
        --p: 400 1.25rem  "Roboto", sans-serif;
        --label: 300 1.25rem  "Roboto", sans-serif;
    }
}